<template>
  <section class="section section-shaped section-lg my-0">
    <div class="shape shape-style-1 bg-gradient-default">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
    <div class="container pt-lg-md">
      <div class="row justify-content-center">
        <div class="col-lg-5">
          <card
            type="secondary"
            shadow
            header-classes="bg-white pb-5"
            body-classes="px-lg-5 py-lg-5"
            class="border-0"
          >
            <template>
              <div class="text-center text-muted mb-4">
                <h4>Sign in with credentials</h4>
              </div>
              <form role="form">
                <base-alert type="primary" v-if="notification">{{ notification }}</base-alert>
                <base-input
                  alternative
                  class="mb-3"
                  placeholder="Email"
                  addon-left-icon="ni ni-email-83"
                  v-model="email"
                  v-focus
                ></base-input>
                <base-input
                  alternative
                  type="password"
                  placeholder="Password"
                  addon-left-icon="ni ni-lock-circle-open"
                  v-model="password"
                  @keyup.enter="submit"
                ></base-input>
                <div class="text-center">
                  <base-button v-on:click="submit" type="primary" class="my-4">Sign In</base-button>
                </div>
              </form>
            </template>
          </card>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from "axios";
import { setTimeout } from "timers";

export default {
  methods: {
    submit(e) {
      axios
        .post("/login", {
          email: this.email,
          password: this.password
        })
        .then(response => {
          if (response.data) {
            this.notification = "Login Successful, Redirecting...";
            setTimeout((window.location = "/app"), 2000);
          } else {
            this.notification = "Email or Password is Incorrect.";
          }
        });
    }
  },
  data() {
    return {
      password: null,
      email: null,
      notification: null
    };
  },
  directives: {
    focus: {
      // directive definition
      inserted: function(el) {
        el.children[1].focus();
      }
    }
  }
};
</script>
