<template>
    <section class="section section-shaped section-lg my-0">
        <div class="shape shape-style-1 bg-gradient-default">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div class="container pt-lg-md">
            <div class="row justify-content-center">
                <div class="col-lg-5">
                    <card type="secondary" shadow
                          header-classes="bg-white pb-5"
                          body-classes="px-lg-5 py-lg-5"
                          class="border-0">
                        
                        <template>
                            <div class="text-center text-muted mb-4">
                                <h4>Sign up with credentials</h4>
                            </div>
                            <form role="form">
                              <base-alert type="primary" v-if="notification">
                                  {{ notification }}
                              </base-alert>
                                <base-input alternative
                                            class="mb-3"
                                            placeholder="Name"
                                            v-model="name"
                                            addon-left-icon="ni ni-hat-3">
                                </base-input>
                                <base-input alternative
                                            class="mb-3"
                                            placeholder="Email"
                                            v-model="email"
                                            addon-left-icon="ni ni-email-83">
                                </base-input>
                                <base-input alternative
                                            v-model="password"
                                            type="password"
                                            placeholder="Password"
                                            addon-left-icon="ni ni-lock-circle-open">
                                </base-input>
                                
                                <base-checkbox>
                                    <span>I agree with the
                                        <a href="#">Privacy Policy</a>
                                    </span>
                                </base-checkbox>
                                <div class="text-center">
                                    <base-button v-on:click="submit" type="primary" class="my-4">Create account</base-button>
                                </div>
                            </form>
                        </template>
                    </card>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      notification: null,
      name: null,
      email: null,
      password: null,
    }
  },
  methods: {
    submit(e) {
      axios.post('/register', {
        name: this.name,
        email: this.email,
        password: this.password,
      })
      .then(response => {
        if (response.data) {
          this.notification = "Account Created.";
        } else {
          this.notification = "Email or Password already exist."
        }
      });
    }
  }
};
</script>
