  methods: {
    getOrders() {
      let quarterlyOrdersIndex = this.quarterlyOrdersIndex;
      let perpetualOrdersIndex = this.perpetualOrdersIndex;
      let cancelledOrdersIndex = this.cancelledOrdersIndex;
      let self = this;
      if (typeof this.episodeOrders.orders.orderStatus != "undefined") {
        let index = 0;
        for (let orders of this.episodeOrders.orders.orderStatus) {
          if (
            orders.quarterlyStatus != null &&
            Object.entries(orders.quarterlyStatus).length > 0 &&
            typeof orders.quarterlyStatus != "undefined"
          ) {
