<template>
  <div class="container-fluid">
    <div class="row flex-xl-nowrap">
      <div class="col-12 col-md-3 col-xl-2 ct-sidebar">
        <nav class="collapse ct-links" id="ct-docs-nav">
          <!-- Show links for all groups -->
          <div class="ct-toc-item active">
            <a class="ct-toc-link" href="#">Deribit Arbitrage</a>
            <ul class="nav ct-sidenav">
              <li class="active ct-sidenav-active">
                <router-link to="/app">Home</router-link>
              </li>
              <li class="ct-sidenav-active">
                <router-link to="/settings">Settings</router-link>
              </li>
              <li class="ct-sidenav-active">
                <router-link to="/calculator">Calculator</router-link>
              </li>
            </ul>
          </div>
        </nav>
      </div>

      <main class="ct-content" role="main">
        <base-alert type="primary" v-if="notification">{{ notification }}</base-alert>
        <h4 id="accountSummary">Account Summary</h4>
        <div class="card-block">
          <div v-if="!account">Loading...</div>
          <account-summary v-else :account="account"></account-summary>
        </div>

        <h4 id="live-data">Live Data</h4>
        <div class="card-block no-padding">
          <live-data :summaries="summaries" :percentDiffs="percentDiffs"></live-data>
        </div>

        <h4 id="form-controls">Episode Form</h4>
        <div v-if="formErrors.length">
          <div v-for="(error, index) in formErrors" :key="index">
            <base-alert type="primary">{{ error }}</base-alert>
          </div>
        </div>
        <div class="card-block">
          <div class="row">
            <div class="col-md-4">
              <base-dropdown v-model="contract1">
                <base-button
                  id="contract1"
                  slot="title"
                  type="primary"
                  class="dropdown-toggle mt-0"
                >Contract #1</base-button>
                <a
                  class="dropdown-item"
                  href="#"
                  v-on:click="dropdownChange"
                  v-for="contract in contracts"
                  :key="contract.instrumentName"
                >{{ contract.instrumentName }}</a>
              </base-dropdown>

              <base-dropdown v-model="contract2">
                <base-button
                  id="contract2"
                  slot="title"
                  type="primary"
                  class="dropdown-toggle mt-0"
                >Contract #2</base-button>
                <a
                  class="dropdown-item"
                  href="#"
                  v-on:click="dropdownChange"
                  v-for="contract in contracts"
                  :key="contract.instrumentName"
                >{{ contract.instrumentName }}</a>
              </base-dropdown>
            </div>

            <div class="col-md-8 rest-form">
              <base-input v-model="episodeSize" placeholder="Episode Size"></base-input>
              <base-input v-model="targetEntry" placeholder="Target Entry % Diff"></base-input>
              <base-input v-model="targetReverse" placeholder="Target Reversal % Diff"></base-input>
            </div>

            <div class="col-md-12">
              <h5>Entry Direction</h5>
              <base-radio name="radio1" class="mb-3" v-model="radio.radio1">High to Low</base-radio>
              <base-radio name="radio2" class="mb-3" v-model="radio.radio1">Low to High</base-radio>
              <base-button block type="primary" icon="ni ni-send" v-on:click="submit">Submit</base-button>
            </div>
          </div>
        </div>

        <h4 id="form-controls">Episodes</h4>
        <div class="card-block">
          <div v-if="!episodeData">Loading...</div>
          <div v-else>
            <episodes
              :episodeData="episodeData"
              :percentDiffs="percentDiffs"
              :positions="positions"
            ></episodes>
          </div>
        </div>
      </main>
    </div>
  </div>
</template>
<script>
import BaseDropdown from "../components/BaseDropdown";
import BaseRadio from "../components/BaseRadio";
import LiveData from "./adminComponents/LiveData";
import AccountSummary from "./adminComponents/AccountSummary";
import Episodes from "./adminComponents/Episodes";
import deribit from "../services/api/deribit";

//import mockEpisodeData from "../assets/MOCK_episodeData.json";

import axios from "axios";
import { setTimeout } from "timers";

export default {
  name: "admin",
  components: {
    BaseDropdown,
    BaseRadio,
    LiveData,
    AccountSummary,
    Episodes
  },
  mounted() {
    this.update();

    deribit.getContracts().then(response => (this.contracts = response));
    deribit.getAccount().then(response => {
      this.account = response.data.result;
    });
    deribit.getActiveEpisode().then(response => {
      this.activeEpisode = response.data;
    });
    axios.get("/logged").then(response => {
      if (!response.data) {
        window.location = "/login";
      }
    });
    this.$nextTick(() => (this.timer = setInterval(this.update, 5000)));
  },
  data() {
    return {
      activeEpisode: null /* () => getActiveEpisode() */,

      mockEpisode: false,
      mockEpisodeData: null,

      episodeData: null,

      summaries: null,
      percentDiffs: null,

      contracts: null,

      notification: null,
      /* formNotification: null, */
      formErrors: [],
      episodeSize: null,
      targetEntry: null,
      targetReverse: null,
      contract1: null,
      contract2: null,
      account: null,
      positions: null,
      radio: {
        radio1: "radio1"
      }
    };
  },
  computed: {
    getActiveEpisode() {
      deribit.getActiveEpisode().then(response => {
        this.activeEpisode = response.data;
      });
    }
  },
  methods: {
    update() {
      deribit.getAllSummary().then(response => {
        this.summaries = response.data.summaries;
        this.percentDiffs = response.data.percentDiffs;
      });

      if (this.mockEpisode == true) {
        this.episodeData = this.mockEpisodeData;
      } else {
        deribit.getActiveEpisode().then(response => {
          this.activeEpisode = response.data;
        });
        this.episodeData = this.activeEpisode;

        let self = this; // wont work without this
        if (this.episodeData != null) {
          this.episodeData.episodes.filter(function(episode) {
            if (episode.started == true && episode.finished == false) {
              deribit.getAccount().then(response => {
                self.account = response.data.result;
              });
              deribit
                .getPositions()
                .then(response => (self.positions = response));
            }
          });
        }
      }
    },
    cancelAutoUpdate: function() {
      clearInterval(this.timer);
    },
    beforeDestory() {
      clearInterval(this.timer);
    },
    checkForm(e) {
      this.formErrors = [];
      console.log(this.contract1);
      console.log(this.contract2);
      if (this.contract1 == "CONTRACT #1") {
        this.formErrors.push("Please select a contract for Contract #1");
      }
      if (this.contract2 == "CONTRACT #2") {
        this.formErrors.push("Please select a contract for Contract #2");
      }
      if (!this.episodeSize) {
        this.formErrors.push(
          "Please enter an number in the Eposide Size field"
        );
      }
      if (!this.targetEntry) {
        this.formErrors.push(
          "Please enter an number in the Target Entry field"
        );
      }
      if (!this.targetReverse) {
        this.formErrors.push(
          "Please enter an number in the Target Entry field"
        );
      }
      /* if (formErrors.length) {
        return false
      } else { 
        return true;
        } */
    },
    submit(e) {
      let direction = null;
      if (this.radio.radio1 === "radio1") {
        direction = 1;
      } else if (this.radio.radio2 === "radio2") {
        direction = -1;
      }

      let formData = {
        targetEntry: this.targetEntry,
        targetReverse: this.targetReverse,
        episodeSize: this.episodeSize,
        contract1: document.getElementById("contract1").innerText,
        contract2: document.getElementById("contract2").innerText,
        direction
      };

      this.contract1 = formData.contract1;
      this.contract2 = formData.contract2;
      this.checkForm(e);
      if (this.formErrors.length) {
        return;
      }

      axios.post("submitepisode", formData).then(response => {
        console.log(response.data);
      });
    },
    dropdownChange(e) {
      e.srcElement.parentElement.previousSibling.innerText =
        e.srcElement.innerText;
    }
  }
};
</script>
