import Vue from "vue";
import Router from "vue-router";
import AppHeader from "./layout/AppHeader";
import AdminHeader from "./layout/AdminHeader";
import AppFooter from "./layout/AppFooter";
import Components from "./views/Components.vue";
import Landing from "./views/Landing.vue";
import Login from "./views/Login.vue";
import Admin from "./views/Admin.vue";
import Settings from "./views/Settings.vue";
import Calculator from "./views/Calculator.vue";
import Register from "./views/Register.vue";
import Profile from "./views/Profile.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  linkExactActiveClass: "active",
  routes: [
    {
      path: "/",
      name: "calculator",
      components: {
        header: AdminHeader,
        default: Calculator
      }
    },
    {
      path: "/landing",
      name: "landing",
      components: {
        header: AppHeader,
        default: Landing,
        footer: AppFooter
      }
    },
    {
      path: "/app",
      name: "app",
      components: {
        header: AdminHeader,
        default: Admin
      }
    },
    {
      path: "/settings",
      name: "settings",
      components: {
        header: AdminHeader,
        default: Settings
      }
    },
    {
      path: "/calculator",
      name: "calculator",
      components: {
        header: AdminHeader,
        default: Calculator
      }
    },
    {
      path: "/login",
      name: "login",
      components: {
        header: AppHeader,
        default: Login,
        footer: AppFooter
      }
    },
    {
      path: "/register",
      name: "register",
      components: {
        header: AppHeader,
        default: Register,
        footer: AppFooter
      }
    },
    {
      path: "/profile",
      name: "profile",
      components: {
        header: AppHeader,
        default: Profile,
        footer: AppFooter
      }
    }
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});
