import axios from "axios";

export default {
  async getContracts() {
    const data = await axios.get("/contracts");
    return data.data;
  },
  async getPositions() {
    const data = await axios.get("positions");
    return data.data;
  },
  async getSummary(instrumentName) {
    const data = await axios.get(`/summary/${instrumentName}`);
    return data.data;
  },
  async getAllSummary() {
    const data = await axios.get("/summary");
    return data;
  },
  async getAccount() {
    const data = await axios.get("/account");
    return data;
  },
  async calculatePercentDiffs() {
    const data = await axios.get("/percentDiffs");
    return data;
  },
  async getActiveEpisode() {
    const data = await axios.get("/episode");
    return data;
  }
};
