<template>
  <div class="container-fluid">
    <div class="row flex-xl-nowrap">
      <div class="col-12 col-md-3 col-xl-2 ct-sidebar">
        <nav class="collapse ct-links" id="ct-docs-nav">
          <!-- Show links for all groups -->
          <div class="ct-toc-item active">
            <a class="ct-toc-link" href="#">Deribit Arbitrage</a>
            <ul class="nav ct-sidenav">
              <li class="ct-sidenav-active">
                <router-link to="/app" >Home</router-link>
              </li>
              <li class="active ct-sidenav-active">
                <router-link to="/settings" >Settings</router-link>
              </li>
              <li class="ct-sidenav-active">
                <router-link to="/calculator" >Calculator</router-link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
      <main class="col-12 col-md-11 col-xl-9 py-md-4 pl-md-5 ct-content" role="main">
        <base-alert type="primary" v-if="notification">
          {{ notification }}
        </base-alert>
        <card type="secondary" shadow
          header-classes="bg-white pb-5"
          body-classes="px-lg-5 py-lg-5"
          class="border-0">
            <template>
                <div class="text-muted mb-4">
                    <h4>Set API Keys</h4>
                </div>
                <form role="form">
                    <base-input alternative
                                class="mb-3"
                                placeholder="Access Key"
                                v-model="accessKey"
                                addon-left-icon="ni ni-key-25">
                    </base-input>
                    <base-input alternative
                                placeholder="Secret Key"
                                v-model="secretKey"
                                addon-left-icon="ni ni-lock-circle-open">
                    </base-input>
                    <base-checkbox v-model="testNet">
                        Enable Testnet 
                    </base-checkbox>
                    <div>
                      <base-button type="primary" v-on:click="submit" class="my-4">Submit</base-button>
                    </div>
                </form>
            </template>
        </card>
          
      </main>
    </div>
  </div>
</template>
<script>
import BaseDropdown from "../components/BaseDropdown";
import BaseRadio from "../components/BaseRadio";
import deribit from "../services/api/deribit";

import axios from "axios";

export default {
  name: "setapi",
  components: {
    BaseDropdown,
    BaseRadio
  },
  mounted(){
    axios.get('/getapi').then(response => {
      this.accessKey = response.data.accessKey;
      this.secretKey = response.data.secretKey;
      this.testNet = response.data.testNet;
    });
  },
  methods: {
    submit(e) {
      axios.post('setapi', {
        accessKey: this.accessKey,
        secretKey: this.secretKey,
        testNet: this.testNet
      })
      .then(response => {
        console.log(response.data);
        if (!response.data ) {
          this.notification = "Something went wrong."
        } else if(response.data.accessKey) {
          this.notification = 'Updated Values: ' + JSON.stringify(response.data)
        } else {
          this.notification = response.data;
        }
      });
    }
  },
  data() {
    return {
      notification: null,
      accessKey: null,
      secretKey: null,
      testNet: null
    };
  }
};
</script>
