<template>
  <div class="container-fluid">
    <div class="row flex-xl-nowrap">
      
      <main class="col-md-11 ct-content" role="main">
        
        <card type="secondary" shadow
          header-classes="bg-white pb-5"
          body-classes="px-lg-5 py-lg-5"
          class="border-0">
            <template>
                <div class="text-muted mb-4">
                    <h4>Profit Calculator with Fees</h4>
                </div>
                <form role="form">
                    <base-input alternative
                                class="mb-3"
                                placeholder="Entry Price"
                                v-model="entry"
                                addon-left-icon="ni ni-curved-next">
                    </base-input>

                    <base-input alternative
                                class="mb-3"
                                placeholder="Exit Price"
                                v-model="exit"
                                addon-left-icon="ni ni-key-25">
                    </base-input>

                    <base-input alternative
                                class="mb-3"
                                placeholder="Contract Size"
                                v-model="contractSize"
                                addon-left-icon="ni ni-money-coins">
                    </base-input>
                      <base-radio name="long" class="mb-3" v-model="radio.long">
                          Long
                      </base-radio>

                      <base-radio name="short" class="mb-3" v-model="radio.long">
                          Short
                      </base-radio>

                     <base-radio name="bothmarket" class="mb-3" v-model="radio.bothmarket">
                        Both Market Order
                     </base-radio>

                      <base-radio name="singlemarket" class="mb-3" v-model="radio.bothmarket">
                          Single Market Order
                      </base-radio>
                    <hr/>
                    <div>
                      <b class="input-value">Your profit will be: </b> 
                        <div><br>
                        <base-button type="primary">Equity Change: {{ profit.equityChange }} BTC</base-button>
                        <base-button type="default">Fees: ${{ profit.fees }}</base-button>
                        <base-button type="info">PnL: {{ profit.pnl }} BTC </base-button>
                        <base-button type="success">Entry BTC Fees: {{ profit.entryBtcFees}} BTC</base-button>
                        <base-button type="danger">Exit Fees: {{ profit.exitBtcFees }} BTC</base-button>
                        <base-button type="warning">Total BTC: {{ profit.totalBtc }} BTC</base-button>

                        </div><br>
                        <base-button block type="primary">Profit USD: ${{ profit.originalProfit }}</base-button>
                    </div>
                    <div>
                      <base-button type="primary" v-on:click="submit" class="my-4">Submit</base-button>
                    </div>
                </form>
            </template>
        </card>
          
      </main>
    </div>
  </div>
</template>
<script>
import BaseDropdown from "../components/BaseDropdown";
import BaseRadio from "../components/BaseRadio";
import deribit from "../services/api/deribit";

import axios from "axios";

export default {
  name: "calculator",

  components: {
    BaseDropdown,
    BaseRadio
  },

  mounted(){
    
  },

  methods: {

    submit(e) {
      this.profit = this.profitFormula(this.entry, this.exit, this.contractSize, this.radio.long, this.radio.bothmarket);
    },

    profitFormula(entry, exit, contractSize, type, bothMarket) {

      if (type ==='short' ) {
        [entry, exit] = [exit, entry]
      }

      let contractUSD = contractSize * 10;
      let takerFee = 0.00075; // 0.075%
      let totalBtc = contractUSD / entry;
      let originalProfit = (exit-entry) * totalBtc; // USD
      let fees = contractUSD * takerFee * 2; // buy, sell included
      
      if (bothMarket === 'singlemarket') {
        fees = contractUSD * takerFee * 1.3333; // buy, sell included
      }
      
      let entryBtcFees = (contractUSD * takerFee) / entry; // BTC
      let exitBtcFees = (contractUSD * takerFee) / exit; // BTC
      let pnl = originalProfit / exit; // BTC
      
      let equityChange = pnl - (entryBtcFees + exitBtcFees);

      let data = {
        totalBtc,
        originalProfit,
        fees,
        equityChange,
        pnl,
        entryBtcFees,
        exitBtcFees
      };

      for(var i in data){
        data[i] = data[i].toFixed(5);
      }

      return data;
    }
  },

  data() {
    return {
      exit: null,
      entry: null,
      contractSize: null,
      profit: {
        totalBtc: null,
        originalProfit: null,
        fees: null,
        equityChange: null,
        pnl: null,
        entryBtcFees: null,
        exitBtcFees: null,
      },
      radio: {
        long: "long",
        bothmarket: "bothmarket"
      }
    };
  }
};
</script>
