    currentPercentDiff(contract1, contract2) {
      if (this.percentDiffs != null) {
        for (let percentDiff of this.percentDiffs) {
          if (percentDiff.contractNames === contract1 + "-" + contract2) {
            return percentDiff.percentDiff;
          }
        }
      }
    },
    absolutePnL(contract1, contract2) {
      let PnLs = [];
      for (let PnL of this.positions.data) {
        PnLs.push(PnL.profitLoss);
      }
      let absPnL = Math.max(...PnLs) + Math.min(...PnLs);
      //const absPnL = PnLs => PnLs.reduce((a, b) => a + b, 0);
      return absPnL;
      /* this.positions.data.filter(function(contract) {
        if (contract.instrument == contract1) {
