    vol5minUsd(instrumentName) {
      let vol5min = "nope";
      let indvArray = ["nothing"];
      let volumeUsd = 0;

      indvArray = Object.values(
        this.summaries.filter(function(contract) {
          if (contract.instrumentName == instrumentName) {
            return contract;
          }
        })
      );

      volumeUsd = indvArray[0].volumeUsd;
      vol5min = volumeUsd / 7200;
      return volumeUsd, Math.floor(vol5min);
    }
